  url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
  url(/static/media/iconfont.92f4817c.woff) format('woff'),
  url(/static/media/iconfont.4fbf35f9.ttf) format('truetype'), 
  url(/static/media/iconfont.7f0966fa.svg#iconfont) format('svg'); /* iOS 4.1- */
}

